
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
  components: {
    XInput,
    XButton,
    Group,
    Cell,
    Flexbox,
    FlexboxItem,
    Toast
  },
  data() {
    return {
      incomes: 0,
      balance: 0,
      history: 0,
      withdrawal: null,
      withdrawalFlag: false,
      doSubmitFlag: false,

      enterprisePayResult: {
        enterprisePays: [],
        page: {}
      }
    }
  },
  mounted(){
    document.getElementById("title").innerHTML = "提现";
    var balance = this.$route.query.balance;
    var history = this.$route.query.history;
    balance ? this.balance = balance : this.balance = 0;
    history ? this.history = history : this.history = 0;

    var _self = this;
    _self.H.toAjax(true).then(function (data) { //data 公用参数
      var submitData = data;
      submitData.data = {
        amount: _self.withdrawal * 100,
        page: {
          lastValue: 0,
          size: 50
        }
      };

      //获取列表
      _self.A.post("/v1/enterprisePay/list", submitData).then(function (result) {
        if (result.data.state.code == 0) {
          _self.enterprisePayResult = result.data.data.enterprisePayResult;
        } else {
          _self.$emit("modalAlert", result.data.state.msg, "确定");
        }
      });
    });
  },
  watch: {
    withdrawal(val){
      if (this.withdrawal != "" && this.withdrawal != null) {
        this.withdrawalFlag = true;
      } else {
        this.withdrawalFlag = false;
      }
    }
  },
  methods: {
    getWithdrawalList(){
      if (this.withdrawal < 2) {
        this.$emit("modalAlert", "最小提取金额为2元", "确定");
        return;
      }
      var _self = this;
      _self.H.toAjax(true).then(function (data) { //data 公用参数
        var submitData = data;
        submitData.data = {
          amount: _self.withdrawal * 100,
          page: {
            lastValue: 0,
            size: 50
          }
        };
        //传提现数量
        _self.A.post("/v1/enterprisePay/withdrawal", submitData).then(function (result) {
          if (result.data.state.code == 0) {
            _self.withdrawal = null;
            _self.doSubmitFlag = true;
          } else {
            _self.$emit("modalAlert", result.data.state.msg, "确定");
          }
        });

        //获取列表
        _self.A.post("/v1/enterprisePay/list", submitData).then(function (result) {
          if (result.data.state.code == 0) {
            _self.enterprisePayResult = result.data.data.enterprisePayResult;
          } else {
            _self.$emit("modalAlert", result.data.state.msg, "确定");
          }
        });
      });
    }
  }
}



